.column{
    width: 33.3%;
    float: right;
}

.img1{
    background: #222;
    position: relative;
    flex: 1;
    max-width: 400px;
    height: 460px;
    margin: 5px;
    margin-top: 40px;
    transition: 0.2s;
    overflow: hidden;
}

.details h2{
    color: greenyellow;
}

.details p{
    color: yellow;
}

.content{
    padding-right: 20px;
    padding-left: 80px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.img1:hover{
    border-radius: 5px;
    box-shadow: 0px 0px 0px 5px;
}

.img1 img{
    opacity: 0.9;
    position: relative;
    vertical-align: top;
    transition: 0.3s;
    transition-property: opacity;
    width: 60%;
    height: 70%;
}