@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@600&family=Grechen+Fuemen&display=swap');

body{
  background: #000;
}

.loader{
  border: 10px solid whitesmoke;
  border-radius: 50%;
  border-top: 10px solid red;
  margin-top: 20%;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 3s linear infinite;
}

@keyframes spin{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}


.container{
  background-size: cover;
  text-align: center;
}

.container1{
  height: auto;
  padding-bottom: 400px;
}

.nav{
  height: 50px;
  background-color: #333;
  padding: 15px;
  overflow: hidden;
  font-weight: bold;
}

.nav .active{
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 25px;
  transition: 0.3s;
}

.nav .active:hover{
  color: yellowgreen;
}

.nav .btn{
  display: none;
  float: right;
  display: block;
  color: #444;
  background-color: greenyellow;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 14px;
  transition: 0.3s;
  border-radius: 10px;
}

.nav .btn:hover{
  /* color: rgb(23,184,63); */
  color: red;
  box-shadow: 0px 0px 0px 2.5px;
}

@media screen and (max-width: 600px) {
  .nav a.btn{
    color: #f2f2f2;
    float: right;
    display: block;
  }

  .container1{
    height: 210vh;
    text-align: -webkit-center;
  }

}

.intro{
  /* top: 40%; */
  width: 80%;
  border-radius: 10px;
  padding: 15px;
  font-family: 'Baloo Bhai 2', cursive;
  font-size: 50px;
  font-style: italic;
  text-decoration: tomato;
  font-weight: 700;
  /* border: 8px solid #282c34; */
  text-shadow: 1px 1px 2px black, 0 0 25px red, 0 0 5px darkred;
  margin: 10% auto 0;
  background: linear-gradient(045deg,(12,11,12,0.555),(59,57,57,0.555));
  color: greenyellow;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
